define('templateSettings',[], function () {

    var defaultTranslationsCode = 'en';

    var defaultTemplateSetting = {
        "logo": {
            "url": ""
        },
        "background": {
            "image": {
                'src': 'css/img/header-bg.jpg?v=1441603027493',
                'type': "fullscreen"
            }
        },
        "xApi": {
            "enabled": true,
            "selectedLrs": "default",
            "lrs": {
                "uri": "",
                "credentials": {
                    "username": "",
                    "password": ""
                },
                "authenticationRequired": false
            },
            "allowedVerbs": []
        },
        "languages": {
            "selected": "",
            "customTranslations": {}
        }
    };


    return {
        init: init,
        logoUrl: 'data:image/png;base64,',
        xApi: {}
    };

    function init(settings) {
        var that = this;
        var fullSettings = _.defaults(settings, defaultTemplateSetting);
        if (!fullSettings.background.image.src) {
            fullSettings.background = defaultTemplateSetting.background
        }

        return Q.fcall(function () {
            //Course logo initialization
            if (!_.isEmpty(fullSettings.logo.url.trim())) {
                that.logoUrl = fullSettings.logo.url;
            }

            that.xApi = fullSettings.xApi;

            that.background = fullSettings.background

        });
    }

});
